@import "../../../utils/variables";

.Friend {
    // border: 1px solid blue;
    margin: 10px 0px;
    background-color: $darker-color;
    color: white;
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 17px;

    & > button {
        font-size: 10px;
    }
    .leftSide {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            margin-right: 5px;
            border-radius: 50%;
        }
    }

    > button.negativeBtn {
        background-color: rgb(185, 57, 57);
        &:hover {
            background-color: rgb(165, 37, 37);
        }
    }
}

@media screen and (max-width: 768px) {
    .Friend {
        flex-direction: column;
        justify-content: space-evenly;

        padding: 8px;
        button {
            margin: 3px;
        }
    }
}
